.project {
	padding: 2em;
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	margin: 0 auto;
	flex-grow: 1;
	text-align: center;
	box-shadow: var(--shadow);
	transition: transform 0.2s linear;
}

.project:hover {
	transform: translateY(-7px);
}

.project__description {
	margin-top: 1em;
}

.project__stack {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	margin: 1.2em 0;
}

.project__stack-item {
	margin: 0.5em;
	font-weight: 500;
	font-size: 0.8rem;
	color: var(--clr-fg-alt);
}

.project .link--icon {
	margin-left: 0.5em;
}
